.shadow {
  /* background-color: #fb0094; */
  /* margin: 200px auto 0; */
  width: 10px;
  position: relative;
  height: 46px;
  /* margin: 200px auto 0; */
}

.shadow:before {
  content: "";
  position: absolute;
  /* top: -2px;
  left: -2px; */
  background: linear-gradient(
    45deg,
    #fb0094,
    #0000ff,
    #00ff00,
    #ffff00,
    #ff0000,
    #fb0094,
    #0000ff,
    #00ff00,
    #ffff00,
    #ff0000
  );
  background-size: 400%;
  width: 100%;
  height: 100%;
  z-index: 10000;
  -webkit-animation: animate 20s linear infinite;
          animation: animate 20s linear infinite;
}

@-webkit-keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 300% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 300% 0;
  }
  100% {
    background-position: 0 0;
  }
}

body {
  background-color: grey;
}

::-webkit-scrollbar-track {
  border: 2px solid blue;
  background-color: transparent;
  border-radius: 20px;
}

::-webkit-scrollbar {
  width: 2px;
  height: 3px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 20px;
  width: 3px;
}

